export default {
	data() { 
		return {
			imgUrl: '/static/danyawenhua.jpg',
			index: '1',
			num: '0',
			isIndex: 1
		}
	},
	methods: {
		closeTip (val) {
			console.log(1)
			this.num = val
			uni.navigateBack({
				delta:1
			})
		},
		back () {
			uni.navigateBack({
				delta:1
			})
		},
		active (val) {
			this.isIndex = val
			if (this.isIndex == 1) {
				console.log('shua')
				uni.scanCode({
					success (res) {
						console.log('条形码类型' + res.scanType)
						console.log('条形码内容' + res.result)
					}
				})
			}
		}
	}
}
